<template>
  <div class="account-safe">
    <div class="item">
      <em>账号</em>
      <p>85C-34343</p>
    </div>
    <div class="item">
      <em>修改密码</em>
      <p class="psd" @click="changePsd">****************</p>
    </div>
    <div class="item">
      <em>版本号</em>
      <p>V1.0.0</p>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    changePsd () {
      this.$router.push({ path: '/changePsd' })
    }
  }
}
</script>

<style lang="stylus" scoped>
.account-safe {
  padding: 50px 20px;
  .item {
    display: flex;
    line-height: 55px;
    border-bottom: 1px solid #ccc;
    em {
      flex: 1;
    }
    .psd {
      padding-right: 30px;
      background: url(../assets/img/right-arrow2.png) no-repeat right center;
      background-size: auto 50%;
    }
  }
}
</style>



